/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Outfit", sans-serif;
  font-size: 1.8rem;
}

a {
  text-decoration: none;
}

img {
  width: 100%;
  height: auto;
}

html {
  line-height: 1.15; /* 1 */
  -webkit-text-size-adjust: 100%; /* 2 */
}

/* Sections
   ========================================================================== */
/**
 * Remove the margin in all browsers.
 */
body {
  margin: 0;
}

/**
 * Render the `main` element consistently in IE.
 */
main {
  display: block;
}

/**
 * Correct the font size and margin on `h1` elements within `section` and
 * `article` contexts in Chrome, Firefox, and Safari.
 */
h1 {
  font-size: 2em;
  margin: 0.67em 0;
}

/* Grouping content
   ========================================================================== */
/**
 * 1. Add the correct box sizing in Firefox.
 * 2. Show the overflow in Edge and IE.
 */
hr {
  box-sizing: content-box; /* 1 */
  height: 0; /* 1 */
  overflow: visible; /* 2 */
}

/**
 * 1. Correct the inheritance and scaling of font size in all browsers.
 * 2. Correct the odd `em` font sizing in all browsers.
 */
pre {
  font-family: monospace, monospace; /* 1 */
  font-size: 1em; /* 2 */
}

/* Text-level semantics
   ========================================================================== */
/**
 * Remove the gray background on active links in IE 10.
 */
a {
  background-color: transparent;
}

/**
 * 1. Remove the bottom border in Chrome 57-
 * 2. Add the correct text decoration in Chrome, Edge, IE, Opera, and Safari.
 */
abbr[title] {
  border-bottom: none; /* 1 */
  text-decoration: underline; /* 2 */
  text-decoration: underline dotted; /* 2 */
}

/**
 * Add the correct font weight in Chrome, Edge, and Safari.
 */
b,
strong {
  font-weight: bolder;
}

/**
 * 1. Correct the inheritance and scaling of font size in all browsers.
 * 2. Correct the odd `em` font sizing in all browsers.
 */
code,
kbd,
samp {
  font-family: monospace, monospace; /* 1 */
  font-size: 1em; /* 2 */
}

/**
 * Add the correct font size in all browsers.
 */
small {
  font-size: 80%;
}

/**
 * Prevent `sub` and `sup` elements from affecting the line height in
 * all browsers.
 */
sub,
sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
}

sub {
  bottom: -0.25em;
}

sup {
  top: -0.5em;
}

/* Embedded content
   ========================================================================== */
/**
 * Remove the border on images inside links in IE 10.
 */
img {
  border-style: none;
}

/* Forms
   ========================================================================== */
/**
 * 1. Change the font styles in all browsers.
 * 2. Remove the margin in Firefox and Safari.
 */
button,
input,
optgroup,
select,
textarea {
  font-family: inherit; /* 1 */
  font-size: 100%; /* 1 */
  line-height: 1.15; /* 1 */
  margin: 0; /* 2 */
}

/**
 * Show the overflow in IE.
 * 1. Show the overflow in Edge.
 */
button,
input { /* 1 */
  overflow: visible;
}

/**
 * Remove the inheritance of text transform in Edge, Firefox, and IE.
 * 1. Remove the inheritance of text transform in Firefox.
 */
button,
select { /* 1 */
  text-transform: none;
}

/**
 * Correct the inability to style clickable types in iOS and Safari.
 */
button,
[type=button],
[type=reset],
[type=submit] {
  -webkit-appearance: button;
}

/**
 * Remove the inner border and padding in Firefox.
 */
button::-moz-focus-inner,
[type=button]::-moz-focus-inner,
[type=reset]::-moz-focus-inner,
[type=submit]::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

/**
 * Restore the focus styles unset by the previous rule.
 */
button:-moz-focusring,
[type=button]:-moz-focusring,
[type=reset]:-moz-focusring,
[type=submit]:-moz-focusring {
  outline: 1px dotted ButtonText;
}

/**
 * Correct the padding in Firefox.
 */
fieldset {
  padding: 0.35em 0.75em 0.625em;
}

/**
 * 1. Correct the text wrapping in Edge and IE.
 * 2. Correct the color inheritance from `fieldset` elements in IE.
 * 3. Remove the padding so developers are not caught out when they zero out
 *    `fieldset` elements in all browsers.
 */
legend {
  box-sizing: border-box; /* 1 */
  color: inherit; /* 2 */
  display: table; /* 1 */
  max-width: 100%; /* 1 */
  padding: 0; /* 3 */
  white-space: normal; /* 1 */
}

/**
 * Add the correct vertical alignment in Chrome, Firefox, and Opera.
 */
progress {
  vertical-align: baseline;
}

/**
 * Remove the default vertical scrollbar in IE 10+.
 */
textarea {
  overflow: auto;
}

/**
 * 1. Add the correct box sizing in IE 10.
 * 2. Remove the padding in IE 10.
 */
[type=checkbox],
[type=radio] {
  box-sizing: border-box; /* 1 */
  padding: 0; /* 2 */
}

/**
 * Correct the cursor style of increment and decrement buttons in Chrome.
 */
[type=number]::-webkit-inner-spin-button,
[type=number]::-webkit-outer-spin-button {
  height: auto;
}

/**
 * 1. Correct the odd appearance in Chrome and Safari.
 * 2. Correct the outline style in Safari.
 */
[type=search] {
  -webkit-appearance: textfield; /* 1 */
  outline-offset: -2px; /* 2 */
}

/**
 * Remove the inner padding in Chrome and Safari on macOS.
 */
[type=search]::-webkit-search-decoration {
  -webkit-appearance: none;
}

/**
 * 1. Correct the inability to style clickable types in iOS and Safari.
 * 2. Change font properties to `inherit` in Safari.
 */
::-webkit-file-upload-button {
  -webkit-appearance: button; /* 1 */
  font: inherit; /* 2 */
}

/* Interactive
   ========================================================================== */
/*
 * Add the correct display in Edge, IE 10+, and Firefox.
 */
details {
  display: block;
}

/*
 * Add the correct display in all browsers.
 */
summary {
  display: list-item;
}

/* Misc
   ========================================================================== */
/**
 * Add the correct display in IE 10+.
 */
template {
  display: none;
}

/**
 * Add the correct display in IE 10.
 */
[hidden] {
  display: none;
}

h1, h2, h3 {
  font-family: "Outfit", sans-serif;
  font-weight: 900;
  margin: 2rem 0;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
}

.text-center {
  text-align: center;
}

[class$=__heading] {
  font-family: "Outfit", sans-serif;
  font-weight: 900;
  margin: 2rem 0;
  margin: 6rem 0 3rem 0;
  text-align: center;
}

[class$=__descripcion] {
  text-align: center;
  font-size: 1.8rem;
  font-weight: 300;
  color: #1E293B;
  margin-bottom: 5rem;
}

.dashboard {
  height: 100%;
  display: flex;
  flex-direction: column;
  background-color: #F8FAFC;
}
.dashboard__header {
  background-color: #062925;
}
@media (min-width: 768px) {
  .dashboard__header {
    padding: 2rem;
  }
}
.dashboard__datos {
  width: min(95%, 140rem);
  margin: 0 auto;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.dashboard__datos p {
  font-weight: 700;
}
.dashboard__datos p span {
  font-weight: 400;
}
.dashboard__texto {
  text-align: center;
}
.dashboard__barra-mobile {
  padding: 2rem 4rem;
  display: flex;
  justify-content: space-between;
  gap: 2rem;
  align-items: center;
}
@media (min-width: 768px) {
  .dashboard__barra-mobile {
    display: none;
  }
}
.dashboard__menu img {
  width: 4rem;
}
.dashboard__menu img:hover {
  cursor: pointer;
}
.dashboard__sidebar-contenedor {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.dashboard__sidebar-contenedor h1, .dashboard__sidebar-contenedor h2 {
  color: #FFFFFF;
  margin: 0;
}
@media (min-width: 768px) {
  .dashboard__sidebar-contenedor {
    display: none;
  }
}
.dashboard__sidebar-cerrar-menu {
  display: flex;
  justify-content: center;
}
.dashboard__sidebar-cerrar-menu:hover {
  cursor: pointer;
}
@media (min-width: 768px) {
  .dashboard__sidebar-cerrar-menu {
    display: none;
  }
}
@media (min-width: 768px) {
  .dashboard__cerrarsesion-mobile {
    display: none;
  }
}
.dashboard__mobile-btncerrarsesion {
  background-color: #062925;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.8rem;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  width: 100%;
  display: inline-block;
}
.dashboard__mobile-btncerrarsesion:hover {
  background-color: black;
  cursor: pointer;
}
.dashboard__usuario {
  display: inline-block;
  text-align: center;
}
.dashboard__usuario p {
  margin: 0;
  color: #FFFFFF;
}
.dashboard__header-grid {
  display: none;
}
@media (min-width: 768px) {
  .dashboard__header-grid {
    display: flex;
    flex-direction: column;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }
}
.dashboard__logo {
  color: #FFFFFF;
  text-align: center;
  margin-bottom: 2rem;
  white-space: nowrap;
  font-size: clamp(4rem, -0.8rem + 10vw, 3rem);
  margin: 0;
  width: 5rem;
}
@media (min-width: 1024px) {
  .dashboard__logo {
    text-align: left;
    margin: 0;
  }
}
.dashboard__logo img {
  color: #FFFFFF;
  text-align: center;
  margin-bottom: 2rem;
  white-space: nowrap;
  font-size: clamp(4rem, -0.8rem + 10vw, 3rem);
  margin: 0;
}
@media (min-width: 1024px) {
  .dashboard__logo img {
    text-align: left;
    margin: 0;
  }
}
.dashboard__nav {
  display: flex;
  justify-content: center;
}
.dashboard__submit--logout {
  color: #FFFFFF;
  font-size: 1.4rem;
  text-transform: uppercase;
  font-weight: 700;
  background: transparent;
  border: none;
}
.dashboard__submit--logout:hover {
  color: #3A9188;
  cursor: pointer;
}
.dashboard__grid {
  display: flex;
  flex: 1;
  overflow-y: auto;
}
.dashboard__sidebar {
  background-color: #044A42;
  opacity: 0;
  height: 0;
  width: 0;
  overflow: hidden;
  transition-property: opacity;
  transition-duration: 500ms;
}
@media (min-width: 768px) {
  .dashboard__sidebar {
    opacity: 1;
    height: 100%;
    width: 15rem;
  }
}
.dashboard__menu {
  padding: 1rem;
  display: flex;
  flex-direction: column;
  gap: 1rem;
}
.dashboard__enlace {
  color: #b5e2dd;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 2rem;
  transition: 300ms background-color ease-in-out, 300ms color ease, border-radius ease;
}
.dashboard__enlace:hover, .dashboard__enlace--actual {
  background-color: #138275;
  color: #FFFFFF;
  border-radius: 1rem;
}
.dashboard__icono {
  font-size: 2rem;
}
.dashboard__menu-texto {
  font-size: 1.1rem;
  text-transform: uppercase;
  font-weight: 700;
  margin-top: 0.5rem;
}
.dashboard__contenido {
  width: 100%;
  padding: 1rem 1rem 3rem 1rem;
  overflow-y: scroll;
}
@media (min-width: 768px) {
  .dashboard__contenido {
    padding: 1rem 5rem 3rem 5rem;
  }
}
@media (min-width: 768px) {
  .dashboard__contenedor-boton {
    display: flex;
    gap: 1rem;
    justify-content: flex-end;
    margin-bottom: 1rem;
  }
}
.dashboard__boton {
  background-color: #044A42;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.8rem;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  width: 100%;
  display: inline-block;
  text-transform: uppercase;
  font-size: 1.4rem;
  margin-bottom: 1rem;
}
.dashboard__boton:hover {
  background-color: #011a17;
  cursor: pointer;
}
.dashboard__boton :last-of-type {
  margin: 0;
}
@media (min-width: 768px) {
  .dashboard__boton {
    width: auto;
  }
}
.dashboard__boton--deshabilitado {
  cursor: not-allowed;
  opacity: 0.5;
}
.dashboard__boton--deshabilitado:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}
.dashboard__deshabilitar {
  opacity: 0.5;
  cursor: not-allowed;
  pointer-events: none;
}
.dashboard__deshabilitar:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}
.dashboard__contenedor {
  box-shadow: rgba(50, 50, 93, 0.25) 0px 13px 27px -5px, rgba(0, 0, 0, 0.3) 0px 8px 16px -8px;
  background-color: #FFFFFF;
  border-radius: 1rem;
  padding: 2rem;
  margin-top: 2rem;
  font-size: 1rem;
}
@media (min-width: 768px) {
  .dashboard__contenedor {
    font-size: 1.8rem;
  }
}
.dashboard__formulario {
  box-shadow: rgba(50, 50, 93, 0.25) 0px 13px 27px -5px, rgba(0, 0, 0, 0.3) 0px 8px 16px -8px;
  background-color: #FFFFFF;
  border-radius: 1rem;
  padding: 2rem;
  margin-top: 2rem;
  width: min(95%, 80rem);
  margin: 2rem auto 0rem auto;
}
.dashboard__formulariopdf {
  box-shadow: rgba(50, 50, 93, 0.25) 0px 13px 27px -5px, rgba(0, 0, 0, 0.3) 0px 8px 16px -8px;
  background-color: #FFFFFF;
  border-radius: 1rem;
  padding: 2rem;
  margin-top: 2rem;
  width: min(95%, 80rem);
  margin: 2rem auto 0rem auto;
  opacity: 0;
  height: 0;
  visibility: hidden;
  transition: visibility 0.3s ease-in-out, opacity 0.5s linear, height 0.5s linear;
}
.dashboard__grafica {
  width: min(95%, 60rem);
  margin: 0 auto;
}
.dashboard__contenedorpdf {
  margin-right: 2rem;
}
.dashboard__contenedorpdf-boton {
  background-color: #3282BB;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.8rem;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  width: 100%;
  display: inline-block;
}
.dashboard__contenedorpdf-boton:hover {
  background-color: #276693;
  cursor: pointer;
}

.mostrarpdf {
  opacity: 1;
  height: auto;
}

.mostrar {
  padding: 3rem;
  opacity: 1;
  height: 100%;
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.9);
}
@media (min-width: 768px) {
  .mostrar {
    display: none;
  }
}

.ocultar {
  opacity: 0;
}

.table {
  width: 100%;
  border-collapse: collapse;
}
.table__formulario--estado {
  padding: 0.5rem 0.3rem;
  border-bottom: 2px solid #59687c;
  margin-bottom: 1rem;
  display: flex;
  align-items: center;
}
.table__formulario--estado:last-of-type {
  border: none;
}
@media (min-width: 768px) {
  .table__formulario--estado {
    padding: 0.5rem 2rem;
  }
}
.table__thead {
  background-color: #1E293B;
  color: #FFFFFF;
}
.table__th {
  padding: 1rem;
  text-align: left;
}
.table__tr:nth-last-of-type(even) {
  background-color: #F8FAFC;
}
.table__td {
  padding: 0;
}
@media (min-width: 768px) {
  .table__td {
    padding: 1.5rem 1rem;
  }
}
.table__td--confirmacion {
  color: #3282BB;
  font-weight: 700;
}
.table__td--noconfirmacion {
  color: rgb(169, 0, 0);
  font-weight: 700;
}
.table__td--acciones {
  display: flex;
  justify-content: flex-end;
  gap: 2rem;
  align-items: center;
}
@media (min-width: 768px) {
  .table__td--acciones {
    padding: 1.5rem 1rem;
  }
}
.table__accion {
  all: unset;
  text-transform: uppercase;
  font-weight: 700;
  cursor: pointer;
}
.table__accion--completada {
  background-color: #3282BB;
  color: #FFFFFF;
  border: none;
  font-size: 0.9rem;
  padding: 0.3rem 1rem;
  text-transform: uppercase;
  font-weight: 700;
  border-radius: 1rem;
}
@media (min-width: 768px) {
  .table__accion--completada {
    padding: 0.5rem 0.8rem;
  }
}
.table__accion--pendiente {
  background-color: #F59E0B;
  color: #FFFFFF;
  border: none;
  font-size: 0.9rem;
  padding: 0.3rem 1rem;
  text-transform: uppercase;
  font-weight: 700;
  border-radius: 1rem;
}
@media (min-width: 768px) {
  .table__accion--pendiente {
    padding: 0.5rem 1rem;
  }
}
.table__accion--editar {
  color: #3282BB;
}
.table__accion--ver {
  color: #3A9188;
}
.table__accion--eliminar {
  color: rgb(169, 0, 0);
}

.paginacion {
  margin-top: 5rem;
  margin-bottom: 3rem;
