  gap: 1rem;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}
@media (min-width: 768px) {
  .paginacion {
    justify-content: flex-end;
  }
}
.paginacion__enlace {
  color: #3282BB;
  font-size: 2rem;
}
.paginacion__enlace--texto {
  padding: 1rem;
  border-radius: 0.5rem;
